import { Component, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'search-box',
  template: require('./search-box.html')
})

export class SearchBox {

  filters = ['Ordem alfabtica', 'Classificao', 'Preo', 'Tempo'];
  search = '';

  @Output() searchValue = new EventEmitter();
  @Output() orderValue = new EventEmitter();

  onChange(deviceValue) {
    switch (deviceValue) {
      case this.filters[0]:
        this.orderValue.emit({ Opc: 'Alpha' });
        return;
      case this.filters[1]:
        this.orderValue.emit({ Opc: 'Class' });
        return;
      case this.filters[2]:
        this.orderValue.emit({ Opc: 'Price' });
        return;
      case this.filters[3]:
        this.orderValue.emit({ Opc: 'Time' });
        return;
    }
  }

  fieldSearch($event) {
    this.search = $event.target.value;
    this.searchValue.emit({ Quest: this.search })
  }

  clearFiedlSearch($event) {
    this.search = '';
    this.searchValue.emit({ Quest: this.search });
  }
}



