import { Component, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'recipe-detail',
  template: require('./recipe-detail.html')
})

export class RecipeDetailComponent {
  /** Varibales inputs **/
  @Input() recipe;
  @Input() fromPlanner;

  /** Variables outputs */
  @Output() recipeDetail = new EventEmitter();
  @Output() addPlanner = new EventEmitter();

  /** hide recipe details **/
  hideRecipe() {
    this.recipeDetail.emit({ recipeDetail: false });
  }
  
  addtoPlanner() {
    this.addPlanner.emit({ addPlanner: true });
  }

}



