import { Component, ElementRef, EventEmitter } from '@angular/core';
import * as $ from 'jquery';

import { AuthService } from '../shared/auth.service';

@Component({
  selector: 'auth-lock',
  template: require('./auth-lock.html')
})
export class AuthLock {
  modal: HTMLElement;
  responseEmitter: EventEmitter<any>;

  constructor(private _authService: AuthService, private _elementRef: ElementRef) { }

  ngOnInit() {
    this._authService.showEmitter.subscribe(res => {
      $(this.modal).show();
      this.responseEmitter = res;
    });
  }

  ngAfterViewInit() {
    this.modal = this._elementRef.nativeElement.children[0];
  }

  close() {
    this.responseEmitter = null;
    $(this.modal).hide();
  }

  authenticate() {
    // (tbragaf, 18/11): Add switch to allow registration
    this._authService.login().then(_ => {
      this.responseEmitter.emit();
      this.close();
    });
  }
}


