import { Injectable, EventEmitter } from '@angular/core';

import { HttpManager } from '../../shared/http.manager';

@Injectable()
export class AuthService {
  showEmitter = new EventEmitter();

  constructor(private _httpManager: HttpManager) { }

  public show() {
    let response = new EventEmitter();
    this.showEmitter.emit(response);
    return response;
  }

  public login() {
    // (tbragaf, 18/11): Consume API. This is but a mock to login promise.
    return new Promise((resolve, reject) => {
      resolve();
    }).then(_ => {
      localStorage.setItem('id_token', 'TOKEN');
    });
  }

  public register() {
    // (tbragaf, 18/11): Consume API. This is but a mock to login promise.
    return new Promise((resolve, reject) => {
      resolve();
    }).then(_ => {
      localStorage.setItem('id_token', 'TOKEN');
    });
  }

  public isLoggedIn() {
    return localStorage.getItem('id_token');
  }

  public logout() {
    localStorage.removeItem('id_token');
  }
}



