export class PricePlan {
  id: Number;
  name: String;
  description: String;
  price: Number;
  isLimited: Boolean;
  features: PricePlanFeature[];

  constructor(id: Number, name: String, description: String, price: Number, isLimited: Boolean, features: PricePlanFeature[]) {
    this.id = id;
    this.name = name;
    this.description = description;
    this.price = price;
    this.isLimited = isLimited;
    this.features = features;
  }
}

export class PricePlanFeature {
  pricePlanId: Number;
  featureId: Number;
  isAvailable: Boolean;

  constructor(pricePlanId: Number, featureId: Number, isAvailable: Boolean) {
    this.pricePlanId = pricePlanId;
    this.featureId = featureId;
    this.isAvailable = isAvailable;
  }
}

export class Feature {
  id: Number;
  name: String;

  constructor(id: Number, name: String) {
    this.id = id;
    this.name = name;
  }
}


