import { Component } from '@angular/core';

import { PricePlan, Feature, PricePlanFeature } from './price-plans-models';

@Component({
  selector: 'price-plans',
  template: require('./price-plans.html')
})
export class PricePlans {
  plans: PricePlan[];
  features: Feature[];

  ngOnInit() {
    this.features = [
      new Feature(0, 'Feature 1'),
      new Feature(1, 'Feature 2')
    ];

    this.plans = [
      new PricePlan(0, 'Grtis', ' tipo grtis', 0, false, [
        new PricePlanFeature(0, 0, true),
        new PricePlanFeature(0, 1, false)
      ]),
      new PricePlan(1, 'Limitado', 'Para os melhores', 1, true, [
        new PricePlanFeature(1, 0, true),
        new PricePlanFeature(1, 1, true)
      ]),
      new PricePlan(2, 'Premium', ' tipo pago', 5, false, [
        new PricePlanFeature(2, 0, true),
        new PricePlanFeature(2, 1, true)
      ])
    ];
  }

  getFeatureName(featureId: Number) {
    return this.features.find(feature => feature.id === featureId).name;
  }

  selectPlan(plan) {
    console.log(`Clicked on ${plan.id}`)
  }
}


