import { Component } from '@angular/core';

import { Feature } from './feature.model';

@Component({
  selector: 'features',
  template: require('./features.html')
})
export class Features {
  features: Feature[];

  ngOnInit() {
    this.features = [
      new Feature(0, 'Feature 1'),
      new Feature(1, 'Feature 2'),
      new Feature(2, 'Feature 3'),
      new Feature(3, 'Feature 4')
    ];

  }
  /*
  getFeatureName(featureId: Number) {
    return this.features.find(feature => feature.id === featureId).name;
  }

  selectPlan(plan) {
    console.log(`Clicked on ${plan.id}`)
  }
  */
}


