import { Component } from '@angular/core';
import * as $ from 'jquery';

import { MealService } from '../shared/meal.service';
import { Meal } from '../shared/meal.model';
import { Recipe } from '../../recipes/shared/recipe.model';

const meals_list: Meal[] = [
  new Meal(1, new Date(2016, 11, 16), 'breakfast', null, null), // new Meal(1, new Date(2016, 11, 16), 'breakfast', null, null),
  //new Meal(1, new Date(2016, 11, 16), 'lunch', null, null),
  new Meal(1, new Date(2016, 11, 16), 'dinner', null, null),
  //new Meal(1, new Date(2016, 11, 17), 'breakfast', null, null),
  new Meal(1, new Date(2016, 11, 17), 'lunch', null, null),
  new Meal(1, new Date(2016, 11, 17), 'dinner', null, null),
]
var p = -1
var pD = 0
const meals_by_date_list = meals_list.reduce(
  (a, b) => {
    if (b.date.getTime() !== pD) {
      pD = b.date.getTime();
      p++;
      a[p] = {};
      a[p].date = b.date;
      a[p]['breakfast'] = null;
      a[p]['lunch'] = null;
      a[p]['dinner'] = null;
    }
    // (tbragaf, 24/11/2016): 
    // a[p][b.mealTime] = b;
    return a;
  }, []);

@Component({
  selector: 'meal-planner',
  template: require('./meal-planner.html'),
})
export class MealPlanner {
  Title = 'Meal Planner';
  Meals = meals_by_date_list;

  constructor(private _mealService: MealService) { }

  test() {
    console.log(meals_by_date_list);
    console.log(meals_by_date_list.length);
  }

  addMeal() {
    console.log("Add Meal");
  }

  removeMeal(MealId) {
    console.log("Remove Meal:" + MealId);
  }
}


