import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

import { HttpManager } from '../../shared/http.manager';
import { Meal } from './meal.model';

@Injectable()
export class MealService {
    httpManager: HttpManager;

    constructor(httpManager: HttpManager) {
        this.httpManager = httpManager;
    }

    getMeals(from: Date, until: Date): Promise<Meal[]> {
        return this.httpManager.get('5835e0e7110000780d0c0033', { from: Date, until: Date })
            .map(value => <Meal[]>value.json())
            .toPromise();
    }
}

